.benefit {
    width: 100%;
    height: 100%;
    padding: 2rem;
    background: #f4f4f4;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    border: 1px solid #000;
  }
  
  .panel-0 .benefit {
    background-color: sienna;
  }
  .panel-1 .benefit {
    background-color: darkolivegreen;
  }
  .panel-2 .benefit {
    background-color: cadetblue;
  }
  
  .panel-3 .benefit {
    background-color: teal;
  }
